<template>
	<div class="info_container1">
		<div class="info_container1_block1">
			<div class="title_big">
				Большой заголовок
			</div>
			<div class="main_text info_container1__main_text">
				Здесь, вблизи сел Вилино и Песчаное, на площади в 370 гектаров раскинулись виноградники винодельческого хозяйства Alma Valley. Ультрасовременный завод построен в лучших традициях европейского виноделия. Уже сейчас Alma Valley перерабатывает и выпускает около 1,5 миллионов бутылок вина в год, при потенциале производства до 3 миллионов бутылок.
			</div>
			<div class="main_text_deactive info_container1__sub_text">
				Сегодня коллекция Alma Valley насчитывает 50 вин девяти различных линеек, различающихся не только по цвету (белый, розовый, красный), но и по сортовому составу, крепости, содержанию остаточного сахара и ценовому сегменту.
			</div>
		</div>		
		<div class="info_container1_block2">
			<div class="image_video_wrapper">
				<div class="image_video_container">
					<img :src="this.api_url + '/static/images/container_img1.jpg'" alt="" />
					<span 
					@click="this.show_main_video = true"
					class="image_video_container__play pointer">
						<PlayIcon className="w-6 h-6" />
					</span>
				</div>
			</div>
		</div>
	</div>

	<div class="main_video_wrapper"
	@click="this.closeVideo()"
	v-show="this.show_main_video">
		<div class="main_video_container">
			<iframe 
			id="main_video"
			src="https://www.youtube.com/embed/C1ABoUlgocU?start=1594" 
			title="YouTube video player" 
			frameborder="0" 
			allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
			allowfullscreen>
			</iframe>
		</div>
		<span 
		@click="this.closeVideo()"
		class="main_video_wrapper__close pointer">
			<XIcon />
		</span>
	</div>
</template>

<script>

import { PlayIcon, XIcon } from '@heroicons/vue/solid'
export default {
	name: 'InfoContainerVideo',
	data () {
		return {
			show_main_video: false,
		}
	},
	components: {
		PlayIcon,
		XIcon,
	},
	computed: {
		api_url () {
			return this.$store.state.api_url;
		}
	},
	methods: {
		closeVideo () {
			var attr = document.getElementById('main_video').getAttribute('src')
			document.getElementById('main_video').setAttribute('src', attr) 
			this.show_main_video = false;
		}
	}
}
</script>
