<template>
  <swiper class="main_page_main_slider"
    @swiper="this.setControlledSwiper"
    :loop="true"
    :slides-per-view="1"
    :space-between="50"
    navigation.nextEl=".main_slider_next"
    navigation.prevEl=".main_slider_prev"
  >
    <swiper-slide
    class="main_page_main_slider_slide">
        <div class="main_slider_text">
            <div class="main_slider_text__title">
            Добро пожаловать!
            </div>
            <div class="main_slider_text__subtitle">
              Зал нашей винокурни выполнен в немецком стиле «фахверк». <br>
              Здесь уютно, комфортно и солнечно!
            </div>
        </div>
        <img :src="this.api_url + '/static/images/B01.jpg'" alt="">
    </swiper-slide>

    <swiper-slide
    class="main_page_main_slider_slide">
        <div class="main_slider_text">
            <div class="main_slider_text__title">
            Добро пожаловать!
            </div>
            <div class="main_slider_text__subtitle">
              Наши напитки изготавливаются исключено на медном оборудовании <br>
              по рецептам, которые передаются столетиями от отца к сыну!
            </div>
        </div>
        <img :src="this.api_url + '/static/images/B05.jpg'" alt="">
    </swiper-slide>

    <div class="main_slider_navigation">
        <div class="main_slider_navigation__button pointer"
        @click="this.slidePrev()">
            <ArrowLeftIcon className="w-6 h-6" />
        </div>
        <div class="main_slider_navigation__button pointer"
        @click="this.slideNext()">
            <ArrowRightIcon className="w-6 h-6" />
        </div>
    </div>

  </swiper>

</template>
<script>
  // Import Swiper Vue.js components
  import { ArrowLeftIcon, ArrowRightIcon } from '@heroicons/vue/solid'
  import SwiperCore, { Navigation } from 'swiper';
  import { Swiper, SwiperSlide } from 'swiper/vue';

  // Import Swiper styles
  import 'swiper/swiper.scss';
  import 'swiper/components/navigation/navigation.scss';

    SwiperCore.use([Navigation])
  export default {
    name: 'MainSlider',
    computed: {
        main_slider_controller () {
            return this.$store.state.main_slider_controller;
        },
        api_url () {
          return this.$store.state.api_url;
        },
    },
    components: {
      ArrowLeftIcon,
      ArrowRightIcon,
      Swiper,
      SwiperSlide,
    },
    methods: {
        setControlledSwiper(swiper) {
            this.$store.commit('setMainSlider', swiper)
        },
        slideNext () {
            this.main_slider_controller.slideNext()
        },
        slidePrev () {  
            this.main_slider_controller.slidePrev()
        }
    },
  };
</script>