<template>
  <div class="second_container">
      <!-- <div class="second_container__background">
          <img :src="api_url + '/static/images/second_container_background.jpg'" alt="">
      </div> -->
      <div class="second_container_main">
        <div class="second_container_main__block">
            <div class="title_big white">
                Вина первого урожая
            </div>
            <div class="main_text2 info_container1__main_text white">
                Вина нашего первого урожая появились в продаже летом 2015 года, с тех пор Alma Valley стала флагманом нового крымского виноделия, завоевав симпатии многотысячной армии ценителей и подтвердив свой уровень более чем 70 наградами, завоеванными на профессиональных винных конкурсах в Москве и Краснодаре, в Гонконге и Лондоне.
            </div>    
            <div class="main_text2 info_container1__main_text white">
                Один из наиболее перспективных участков для нового виноградника был найден в Западно-Предгорном Крыму, в долине реки Альма, вошедшей в историю, как благодатный край для виноградарства, зона обитания древних скифов и место Альминского сражения 1854 года – важной вехи Крымской войны 1853–1856 годов.
            </div>    
        </div>
        <div class="second_container_main__block">
            <swiper class="second_container_slider"
                @swiper="this.setControlledSwiper"
                :loop="true"
                :slides-per-view="1"
                :space-between="50"
                :autoplay="true"
                navigation.nextEl=".main_slider_next"
                navigation.prevEl=".main_slider_prev"
                
                >
                <swiper-slide
                class="second_container_slider_item">
                    <img :src="api_url + '/static/images/container_img1.jpg'" alt="">
                </swiper-slide>
            </swiper>
            <div class="second_slider_navigation">
                    <div class="main_slider_navigation__button pointer"
                    @click="this.slidePrev()">
                        <ArrowLeftIcon className="w-6 h-6" />
                    </div>
                    <div class="main_slider_navigation__button pointer"
                    @click="this.slideNext()">
                        <ArrowRightIcon className="w-6 h-6" />
                    </div>
                </div>
        </div>
      </div>
  </div>
</template>

<script>

import SwiperCore, { Navigation, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';

import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';

import { ArrowLeftIcon, ArrowRightIcon } from '@heroicons/vue/solid'

SwiperCore.use([Navigation, Autoplay ])

export default {
    name: "InfoContainerSecond",
    components: {
        Swiper, SwiperSlide,
        ArrowLeftIcon, ArrowRightIcon,
    },
    computed: {
        api_url () {
            return this.$store.state.api_url
        },
        second_slider_controller () {
            return this.$store.state.second_slider_controller;
        }
    },
    methods: {
        setControlledSwiper(swiper) {
            this.$store.commit('setSecondSlider', swiper)
        },
        slideNext () {
            this.second_slider_controller.slideNext()
        },
        slidePrev () {  
            this.second_slider_controller.slidePrev()
        }
    }
}
</script>

